.decor {
  position: relative;
  width: 100%;
  max-width: 400px;
  background: white;
  border-radius: 30px; }

.form-left-decoration,
.form-right-decoration {
  content: '';
  position: absolute;
  width: 50px;
  height: 20px;
  background: #6297ff;
  border-radius: 20px; }

.form-left-decoration {
  bottom: 60px;
  left: -30px; }

.form-right-decoration {
  top: 60px;
  right: -30px; }

.form-left-decoration:before,
.form-left-decoration:after,
.form-right-decoration:before,
.form-right-decoration:after {
  content: '';
  position: absolute;
  width: 50px;
  height: 20px;
  border-radius: 30px;
  background: white; }

.form-left-decoration:before {
  top: -20px; }

.form-left-decoration:after {
  top: 20px;
  left: 10px; }

.form-right-decoration:before {
  top: -20px;
  right: 0; }

.form-right-decoration:after {
  top: 20px;
  right: 10px; }

.circle {
